/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";

import Banner from '../../components/Banner/Banner'

import { authStore } from '../../store/authStore'
import { formatString } from '../../store/store'
import { mediaLinks } from '../../store/store'
import './styles.sass'

const Home = () =>{
    const [ytStats,setYtStats] = useState([])
    const [spotifyStats,setSpotifyStats] = useState([])
    const [instagramPosts,setInstagramPosts] = useState([])
    const [scroll,setScroll] = useState(0)

    const html = document.getElementsByTagName('html')[0]

    useEffect(()=>{
        const event = () => setScroll(html.scrollTop)
        
        window.addEventListener('scroll', event)
        
        return () => window.removeEventListener('scroll',event)
    },[])

    useEffect(()=>{
        (async()=>{
            const yt_response = await authStore.fetchYoutubeArtist()
            setYtStats(yt_response.items[0].statistics)

            const instagram_response = await authStore.fetchInstagramPosts()

            const instagram_posts = []
            let count = 0

            while(instagram_posts.length < 6){
                console.log(instagram_response[count])
                if(instagram_response[count].media_type === 'IMAGE'){
                    instagram_posts.push(instagram_response[count])
                }   
                count++
            }

            setInstagramPosts(instagram_posts)

            setSpotifyStats([
                {name: 'Słuchaczy w tym miesiącu', number: '10071'},
                {name: 'Utworów', number: '6'},
                {name: 'Obserwujących', number: `260`},
            ])
        })()
    },[])

    return (
        <div className='home-container'>
            <img 
                style={scroll > 1000 ? { display: 'none' } : {}} 
                src='./assets/background.png'
                className='background-img'
                alt='background'
            />
            <div className='background-wrapper'>
                <div className='background-description'>
                    <div className='h1'>NotSoTam</div>
                    <div className='h1'>stestuj to</div>
                    <div className='h2'>Grupa młodych adeptów krakowskiego undergroundu</div>
                </div>
            </div>
            <div className='content'>

                <div className='general-info'>
                    <img src='./assets/background-2.jpg' alt='img'/>
                    <div className='description'>
                        <div className='title'>NotSoTam</div>
                        <div className='text'>
                            NotSoTam to paczka przyjaciół, których połączyła miłość do muzyki.
                            Nazwa ta była wynikiem ewolucji sformułowania "no co tam", które po raz pierwszy zostało użyte przez Młodego Fajkę,
                            jako jego odpowiedź dla fanów tak zwanej taniej bani. Był to utwór zatytułowany "Herman Muller" na cześć upadłego 
                            króla najtańszych browarów. Uwielbiany niegdyś przez osoby koczujące pod sklepami z logiem żaby został zapomniany i 
                            zastąpiony nowym, znanym wszystkim graczem - Romperem. Debiutancki utwór w krótkim czasie zyskał szerokie poparcie, co 
                            popchnęło Młodego Fajkę do dalszego działania i rozwoju. Chcąc poprawić wyniki i wznieść się na kolejny poziom 
                            zaprojektował on logo na podstawie paczki papierosów Marlboro, oraz nadał nową nazwę "NotSoTam", która miała symbolizować 
                            zmiany. W wolnym tłumaczeniu "not so" - "bynajmniej" oznacza bynajmniej nie to co kiedyś "nocotam".
                            Kluczowym momentem dla całej paczki był utwór "COVID-19", który zjednoczył wielu twórców i dał początek istnieniu nowego tworu,
                            który nie stanowiła już jedna osoba a cztery.
                        </div>
                    </div>
                </div>

                <Banner/>

                <div className='spotify-label'>
                    Posłuchaj nas na Spotify
                </div>
                <div className='spotify-description'>
                    Niedawno rozszerzyliśmy naszą działalność o platformy streamingowe, między innymi: Spotify, Apple Music i inne.
                    Dołącz do grona naszych słuchaczy i posmakuj muzyki w najlepszej jakości bez zbędnych reklam.
                </div>
                <div className='spotify-stats'>
                    {
                        spotifyStats.map(stat=>
                            <div className='stat' key={stat.name}>
                                <div className='name'>{stat.name}</div>
                                <div className='number'>
                                    {stat?.number ? formatString(stat.number) : ''}
                                </div>
                            </div>
                        )
                    }
                </div>
                <iframe
                    src="https://open.spotify.com/embed/artist/42spJqYXwiMbi6p4GFctJP?utm_source=generator"
                    allow="autoplay; clipboard-write; encrypted-media; fullscreen; picture-in-picture"
                    className='spotify'
                    allowFullScreen="" 
                    title="spotify"
                    frameBorder="0" 
                ></iframe>

                <div className='youtube-wrapper'>
                    <img alt='yt-background' src='./assets/yt-background.png'/>
                    <div className='label'>
                        <img src='./assets/yt-logo.jpg' alt='logo'/>
                        <div className='title'>NotSoTam</div>
                        <div className='stats'>
                            <div className='stat'>
                                {`subskrybcje: ${ytStats?.subscriberCount ? formatString(ytStats?.subscriberCount):''}`}
                            </div>
                            <div className='stat'>
                                {`wyświetlenia: ${ytStats?.viewCount ? formatString(ytStats?.viewCount):''}`}
                            </div>
                        </div>
                    </div>
                    <iframe
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" 
                        src="https://www.youtube.com/embed/iughcanr7nk" 
                        title="YouTube video player" 
                        className="youtube"
                        frameBorder="0" 
                        allowFullScreen
                    ></iframe>
                </div>

                <div className='instagram-container'>
                    <div className='title'>
                        <img
                            onClick={()=> window.open(mediaLinks[0].to,'_blank')}
                            src='./assets/instagram.svg'
                            alt=''
                        />
                        <div>Sprawdź naszego instagrama</div>
                    </div>
                    <div className='instagram-wrapper'>
                        {
                            instagramPosts.map(post =>
                                <img src={post.media_url} alt='post' clasName='media'/>
                            )
                        }
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Home